import { mapState } from 'vuex';
import 	interfaces from '../../utils/interface.js'








export default{
	data(){
		return{
			serviceShow : false,
			InfoShow : false,
			jifen : 0,
			kan_priceAll : '',
			user_info : '',
			user_phone : '',	
		}
	},
	computed: {
		...mapState(['hasLogin' , 'userInfo'])
	},
	methods:{
		navTo(url){
			if(!this.hasLogin){
				url = '/pages/public/login';
			}
			uni.navigateTo({
				url
			})
		},
		off(status){ //关闭客服
			this.serviceShow = status;
		},
		offInfo(status){ //
			
			 this.InfoShow = status;
			// 跳转 H5 支付
			// let url = 'http://www.mamamohe.cn/wx/pay.php';
			// let url = 'http://www.mamamohe.cn/zfb/alipay/index.php';
			// void plus.runtime.openWeb(url , function(){
			// 	console
			// })
		},
		async initData(){
			this.request({
				url: interfaces.get_jifen, //获取用户积分
				method: "POST",
				success: ((res) => {
					console.log(res)
					if(res.err_code == 1){
						this.jifen = res.data;
					}
				})
			});
			this.request({
				url: interfaces.get_news, //获取用户消息
				method: "POST",
				success: ((res) => {
					console.log(res)
					this.user_info = res.data;
				})
			});
			this.request({
				url: interfaces.get_kefu, //获取客服电话
				method: "POST",
				success: ((res) => {
					this.user_phone = res.data;
				})
			});
			this.request({
				url: interfaces.get_kanPrice, //获取砍价金额
				method: "POST",
				success: ((res) => {
					if(res.error_code == 0){
						this.kan_priceAll = res.data;
					}
				})
			})
















		}
	},
	onLoad() {
		this.initData()
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(() => {
			uni.stopPullDownRefresh();
			this.initData();
		}, 1000);
	},
}
